@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}

@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/open-sans-300.woff2") format("woff2"), url("../fonts/open-sans-300.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/open-sans-regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-700.woff2") format("woff2"), url("../fonts/open-sans-700.woff") format("woff");
}
.container {
  width: 320px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}
@media (min-width: 660px) {
  .container {
    width: 660px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-right: 130px;
    padding-left: 130px;
  }
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: "Open Sans", "Verdana", sans-serif;
  font-size: 14px;
  color: #283645;
}

.page__header,
.page__footer {
  display: grid;
  grid-template-columns: 1fr 280px 1fr;
  column-gap: 20px;
}
@media (min-width: 660px) {
  .page__header,
  .page__footer {
    grid-template-columns: 1fr 620px 1fr;
    column-gap: 20px;
  }
}
@media (min-width: 1200px) {
  .page__header,
  .page__footer {
    grid-template-columns: 1fr 940px 1fr;
    column-gap: 130px;
  }
}

.page__header {
  position: absolute;
}

.page__nav,
.page__footer-content {
  grid-column: 2/3;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
}

.page__title {
  max-width: 280px;
  margin: 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
}
@media (min-width: 660px) {
  .page__title {
    max-width: 620px;
    font-size: 40px;
    line-height: 54px;
  }
}
@media (min-width: 1200px) {
  .page__title {
    max-width: 940px;
    font-size: 50px;
    line-height: 68px;
  }
}

.header {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (max-width: 1199px) {
  .header--active {
    background-color: #283645;
  }
}
@media (max-width: 1199px) {
  .header--nojs {
    position: initial;
    background-color: #283645;
  }
}

.body {
  font-size: 14px;
  line-height: 24px;
}

.footer__content {
  display: grid;
  justify-items: center;
  align-items: center;
  padding: 40px 0;
  grid-template-rows: repeat(3, min-content);
}
@media (min-width: 660px) {
  .footer__content {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: initial;
    padding: 36px 0;
  }
}
@media (min-width: 1200px) {
  .footer__content {
    padding: 71px 0;
  }
}

.footer__logo,
.footer__copyright {
  width: 100%;
  justify-content: center;
  align-items: center;
}

@media (max-width: 659px) {
  .footer__logo {
    margin-bottom: 38px;
  }
}
@media (min-width: 660px) {
  .footer__logo {
    margin-top: -14px;
    justify-content: initial;
    align-items: initial;
  }
}

.footer__list {
  margin: 0;
}

@media (max-width: 659px) {
  .footer__copyright {
    margin-top: 38px;
  }
}
@media (min-width: 660px) {
  .footer__copyright {
    justify-content: right;
  }
}

.arrows {
  display: none;
}
@media (min-width: 1200px) {
  .arrows {
    display: flex;
    justify-content: space-between;
  }
}

.arrows__btn {
  width: 21px;
  height: 41px;
  padding: 0;
  border: 0;
  mask-position: center;
  mask-repeat: no-repeat;
  background-color: rgba(40, 54, 69, 0.3);
}
.arrows__btn:hover {
  cursor: pointer;
  background-color: rgba(40, 54, 69, 0.6);
}
.arrows__btn:active {
  background-color: rgba(40, 54, 69, 0.1);
}
.arrows__btn--prev {
  mask-image: url("../img/ico/arrow.svg");
  transform: scaleX(-1);
}
.arrows__btn--next {
  mask-image: url("../img/ico/arrow.svg");
}

.togglers {
  display: flex;
  height: 12px;
  align-items: center;
  justify-content: center;
  column-gap: 13px;
  padding: 0;
  list-style-type: none;
}

.togglers__btn {
  position: relative;
  display: block;
  width: 12px;
  height: 12px;
  padding: 0;
  border: 0;
  border-radius: 50%;
  background-color: transparent;
}
.togglers__btn::after {
  content: "";
  position: absolute;
  width: 6px;
  height: 6px;
  top: 25%;
  left: 25%;
  border-radius: 50%;
  background-color: rgba(40, 54, 69, 0.3);
}
.togglers__btn:hover {
  cursor: pointer;
}
.togglers__btn:hover::after {
  background-color: rgba(40, 54, 69, 0.6);
}
.togglers__btn:active::after {
  background-color: rgba(40, 54, 69, 0.1);
}
.togglers__btn--active {
  pointer-events: none;
  background-color: #ffffff;
  outline: 3px solid #283645;
  outline-offset: -3px;
}
.togglers__btn--active::after {
  content: none;
}

.logo {
  display: flex;
}
.logo:not(:link) {
  pointer-events: none;
}
.logo:hover {
  filter: brightness(0) saturate(100%) invert(24%) sepia(48%) saturate(4325%) hue-rotate(328deg) brightness(86%) contrast(91%);
}
.logo--header:active {
  filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(0%) hue-rotate(116deg) brightness(103%) contrast(105%) opacity(30%);
}
.logo--footer:active {
  filter: opacity(30%);
}

.logo__image {
  display: block;
}
@media (min-width: 660px) {
  .logo__image {
    width: 145px;
  }
}
@media (min-width: 1200px) {
  .logo__image {
    width: 147px;
  }
}

.hero {
  color: #ffffff;
  background-color: #1d2631;
}

.hero__button {
  width: 100%;
  margin-bottom: 20px;
}
@media (min-width: 660px) {
  .hero__button {
    margin-bottom: 34px;
  }
}
@media (min-width: 1200px) {
  .hero__button {
    margin-bottom: 55px;
  }
}

.hero__list {
  margin: 0;
  display: flex;
  align-items: center;
  padding: 0;
  list-style-type: none;
}

.hero__item {
  display: block;
}

.hero__link {
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}
.hero__link--apple {
  width: 83px;
  height: 80px;
  background-image: url("../img/logos/apple.svg");
}
.hero__link--google {
  width: 79px;
  height: 80px;
  background-image: url("../img/logos/android.svg");
}
.hero__link--ms {
  width: 80px;
  height: 80px;
  background-image: url("../img/logos/microsoft.svg");
  background-position-y: 23px;
}

.hero__note {
  font-weight: 300;
  margin: 10px 0 0;
  text-align: center;
}
@media (min-width: 660px) {
  .hero__note {
    margin: 21px 0 0;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .hero__note {
    margin: 39px 0 0;
  }
}

.hero-index {
  display: grid;
  grid-template-columns: 1fr 280px 1fr;
  grid-template-rows: 462px 1fr;
  column-gap: 20px;
  background-image: url("../img/index/promo-hand-mobile@1x.png"), url("../img/index/index-back-mobile@1x.jpg");
  background-repeat: no-repeat, no-repeat;
  background-position: 50% 176px, center 0;
  background-size: auto, auto 462px;
}
@media (min-width: 660px) {
  .hero-index {
    grid-template-columns: 1fr repeat(2, 300px) 1fr;
    grid-template-rows: min-content;
    min-height: 729px;
    background-image: url("../img/index/promo-hand-tablet@1x.png"), url("../img/index/index-back-tablet@1x.jpg");
    background-position: 50% bottom, center 0;
    background-size: auto, cover;
  }
}
@media (min-width: 1200px) {
  .hero-index {
    grid-template-columns: 1fr repeat(3, 300px) 1fr;
    min-height: 853px;
    background-image: url("../img/index/promo-hand-desktop@1x.png"), url("../img/index/index-back-desktop@1x.jpg");
    background-position: calc(50% + 70px) bottom, center 0;
  }
}

.hero-index__content {
  grid-column: 2/3;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 31px;
}
@media (min-width: 660px) {
  .hero-index__content {
    padding: 31px 0;
  }
}
@media (min-width: 1200px) {
  .hero-index__content {
    padding-top: 36px;
  }
}

.hero-index__title {
  grid-column: 2/3;
  padding-top: 89px;
  overflow-y: hidden;
}
@media (min-width: 660px) {
  .hero-index__title {
    overflow-y: initial;
    line-height: 48px;
    padding-top: 203px;
  }
}
@media (min-width: 1200px) {
  .hero-index__title {
    padding-top: 209px;
    line-height: 68px;
  }
}

.hero-other {
  display: flex;
  min-height: 237px;
  padding-top: 95px;
  padding-bottom: 40px;
  background-image: url("../img/index/other-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center top;
}
@media (min-width: 660px) {
  .hero-other {
    min-height: 440px;
    padding-top: 177px;
    background-image: url("../img/index/other-back-tablet@1x.jpg");
  }
}
@media (min-width: 1200px) {
  .hero-other {
    min-height: 394px;
    padding-top: 169px;
    background-image: url("../img/index/index-back-desktop@1x.jpg");
  }
}

.hero-other__title {
  margin: 0 auto;
}
@media (min-width: 660px) {
  .hero-other__title {
    font-size: 50px;
    line-height: 68px;
  }
}

.nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .nav {
    justify-content: space-between;
    flex-direction: row;
  }
}

.nav__top-bar {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 66px;
  align-items: center;
}
@media (min-width: 660px) {
  .nav__top-bar {
    height: 110px;
  }
}
@media (min-width: 1200px) {
  .nav__top-bar {
    width: fit-content;
    flex-shrink: 0;
  }
}

@media (min-width: 660px) {
  .nav__logo {
    margin-top: -16px;
  }
}

.nav__list {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  top: 66px;
  display: none;
  flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: #283645;
  list-style-type: none;
}
@media (min-width: 660px) {
  .nav__list {
    top: 110px;
  }
}
@media (min-width: 1200px) {
  .nav__list {
    position: unset;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-right: -40px;
    padding: 18px 0 16px;
    background-color: transparent;
  }
}
.nav__list--active {
  display: flex;
}
@media (max-width: 1199px) {
  .nav__list--nojs {
    display: flex;
    position: initial;
  }
  .nav__list--nojs .nav__item::before, .nav__list--nojs .nav__item::after {
    left: -30vw;
    right: -30vw;
  }
  .nav__list--nojs .nav__item:last-of-type {
    box-shadow: none;
  }
}

.nav__item {
  position: relative;
  display: flex;
  align-items: flex-start;
}
@media (max-width: 1199px) {
  .nav__item::before, .nav__item::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
  }
  .nav__item::before {
    top: 1px;
    background-color: rgba(255, 255, 255, 0.15);
  }
  .nav__item::after {
    background-color: #000000;
  }
  .nav__item:last-of-type {
    box-shadow: 0 1px 0 #000000;
  }
}

.nav__link {
  width: 100%;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  padding: 16px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 1200px) {
  .nav__link {
    font-weight: 400;
    padding: 20px 40px;
    text-transform: initial;
  }
}
.nav__link:hover {
  color: #d22856;
}
.nav__link:active {
  color: rgba(255, 255, 255, 0.3);
}
.nav__link--current {
  position: relative;
  pointer-events: none;
}
@media (max-width: 1199px) {
  .nav__link--current {
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 1200px) {
  .nav__link--current::after {
    content: "";
    position: absolute;
    bottom: 7px;
    left: 40px;
    right: 40px;
    height: 2px;
    background-color: #ffffff;
  }
}

.nav__toggler {
  border: none;
  padding: 0;
  width: 90px;
  height: 66px;
  margin-right: -20px;
  background-color: #ffffff;
  mask: url("../img/ico/btn-menu-open.svg") no-repeat center;
}
@media (min-width: 1200px) {
  .nav__toggler {
    display: none;
  }
}
.nav__toggler:hover {
  cursor: pointer;
  background-color: #d22856;
}
.nav__toggler:active {
  background-color: rgba(210, 40, 86, 0.3);
}
.nav__toggler--active {
  width: 63px;
  mask: url("../img/ico/btn-menu-close.svg") no-repeat center;
}

.advantages__title-wrapper {
  padding-top: 39px;
  padding-bottom: 28px;
}
@media (min-width: 660px) {
  .advantages__title-wrapper {
    padding-top: 46px;
    padding-bottom: 30px;
  }
}
@media (min-width: 1200px) {
  .advantages__title-wrapper {
    padding-top: 63px;
    padding-bottom: 49px;
  }
}

.advantages__title {
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 659px) {
  .advantages__title + .advantages__title {
    margin-top: 30px;
  }
}

.advantages__list-wrapper {
  position: relative;
  padding-top: 85px;
  background-color: #f2f2f2;
}
.advantages__list-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 35px;
  background: linear-gradient(to right bottom, #ffffff 50%, #f2f2f2 50%);
}
@media (min-width: 660px) {
  .advantages__list-wrapper::before {
    height: 53px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-wrapper::before {
    height: 100px;
  }
}
@media (min-width: 660px) {
  .advantages__list-wrapper {
    padding-top: 76px;
    padding-bottom: 44px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-wrapper {
    padding-top: 307px;
    padding-bottom: 67px;
  }
}

.advantages__list {
  position: relative;
  display: grid;
  row-gap: 32px;
  list-style-type: none;
}
@media (min-width: 660px) {
  .advantages__list {
    min-height: 367px;
    grid-template-columns: 310px 1fr;
    row-gap: 35px;
  }
}
@media (min-width: 1200px) {
  .advantages__list {
    min-height: initial;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    row-gap: 340px;
  }
}

.advantages__item {
  position: relative;
}
@media (min-width: 660px) {
  .advantages__item {
    position: initial;
    grid-column: 2/3;
  }
}
@media (min-width: 1200px) {
  .advantages__item {
    position: relative;
    grid-column: initial;
    padding-top: 56px;
  }
}
@media (min-width: 1200px) {
  .advantages__item::before {
    content: "";
    position: absolute;
    top: -15px;
    width: 100%;
    height: 30px;
    z-index: 1;
    background-image: radial-gradient(closest-side, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0));
    clip-path: inset(0 0 50% 0);
  }
}
.advantages__item::after {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
  background-position: top center;
}
@media (min-width: 1200px) {
  .advantages__item::after {
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    width: 186px;
    height: 300px;
  }
}
@media (max-width: 659px) {
  .advantages__item--emphasis {
    order: 1;
  }
}
@media (max-width: 659px) {
  .advantages__item--emphasis::after {
    position: relative;
    display: block;
    width: 280px;
    height: 277px;
    margin-top: 41px;
    background-image: url("../img/index/phone-mobile@1x.png");
  }
}
@media (min-width: 660px) and (max-width: 1199px) {
  .advantages__item--emphasis::after {
    width: 300px;
    height: 481px;
    bottom: -44px;
    left: 20px;
    background-image: url("../img/index/phone-tablet@1x.png");
    background-position: top left;
  }
}
@media (min-width: 1200px) {
  .advantages__item--01::after {
    background-image: url("../img/index/phone-desktop-01@1x.png");
  }
}
@media (min-width: 1200px) {
  .advantages__item--02::after {
    background-image: url("../img/index/phone-desktop-02@1x.png");
  }
}
@media (min-width: 1200px) {
  .advantages__item--03::after {
    background-image: url("../img/index/phone-desktop-03@1x.png");
  }
}

@media (min-width: 660px) {
  .advantages__add-wrapper {
    padding-top: 54px;
  }
}
@media (min-width: 1200px) {
  .advantages__add-wrapper {
    padding-top: 0;
    padding-bottom: 67px;
    background-color: #f2f2f2;
  }
}

.advantages__add {
  display: none;
}
@media (min-width: 660px) {
  .advantages__add {
    display: block;
    position: relative;
    min-height: 276px;
    padding-top: 41px;
    padding-left: 330px;
  }
}
@media (min-width: 1200px) {
  .advantages__add {
    min-height: 345px;
    padding-top: 95px;
    padding-left: 500px;
  }
}
.advantages__add::before {
  content: "";
  position: absolute;
  width: 238px;
  height: 276px;
  top: 0;
  left: 20px;
  background-image: url("../img/logos/timer.svg");
  background-repeat: no-repeat;
  background-position: left;
  background-size: cover;
}
@media (min-width: 1200px) {
  .advantages__add::before {
    width: 298px;
    height: 345px;
    left: 130px;
  }
}

.advantages__name {
  margin: 0;
  margin-bottom: 11px;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: #000000;
}
@media (min-width: 660px) {
  .advantages__name {
    text-align: left;
  }
}
@media (min-width: 1200px) {
  .advantages__name {
    margin-bottom: 23px;
    font-size: 30px;
    line-height: 1;
    text-align: center;
  }
}
.advantages__name--add {
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 11px;
}
@media (min-width: 1200px) {
  .advantages__name--add {
    margin-bottom: 26px;
    line-height: 1;
    text-align: left;
  }
}
@media (min-width: 1200px) {
  .advantages__name > br {
    display: none;
  }
}

.advantages__desc {
  margin: 0;
  text-align: center;
}
@media (min-width: 660px) {
  .advantages__desc {
    text-align: left;
  }
}
@media (min-width: 1200px) {
  .advantages__desc {
    font-size: 18px;
    line-height: 30px;
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .advantages__item > .advantages__desc {
    padding: 0 20px;
  }
}
@media (min-width: 1200px) {
  .advantages__name--add + .advantages__desc {
    text-align: left;
  }
}

@media (min-width: 1200px) {
  .advantages__additional {
    background-color: #f2f2f2;
  }
}

.advantages__link {
  display: block;
  margin-top: 11px;
  color: #d22856;
}
@media (min-width: 1200px) {
  .advantages__link {
    font-size: 18px;
    margin-top: 33px;
  }
